/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {

    margin-bottom: 40px;

    .eltd-content-bottom &,
    .eltd-page-content-holder & {
        margin-bottom: 0;
    }
	
	.eltd-widget-title {
		margin: 0 0 15px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .eltd-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_categories{

        .eltd-widget-title{

            margin-bottom: 13px;
        }


        ul {

            li {
                color:$default-text-color !important;

                a {

                    &:hover{
                        color: $default-menu-color;  /* Opacity for widget categories*/
                    }

                }

            }

        }


    }


    &.widget_search {

        .eltd-widget-title{

            margin-bottom: 22px;
        }

        form {
            @include eltdRelativeHolderLayout();
            clear: both;

            > div {
                @include eltdRelativeHolderLayout();
                border-width: 1px;
                border-style: solid;
                border-color: $default-border-color;
                box-sizing: border-box;
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 52px;
            font-family: inherit;
            font-weight: 400;
            font-size: 15px;
            line-height: 52px;
            color: $default-info-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include eltdTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 40px);
            padding: 0 0 0 21px;

	
	        @include eltdPlaceholder {
		        color: inherit;
	        }
        }

        button {
            width: 40px;
            font-size: 15px;
            color: $default-info-color;
            cursor: pointer;
            @include eltdTransition(color .2s ease-in-out);
            @include eltdTransform(rotate(270deg));
            margin-right: -5px;

            &:hover {
                opacity:0.85;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 4px 0;
                margin:0;
                text-transform: capitalize;
                font-size: 15px;
                display: block;
                font-weight: 400;
                color:$default-info-color;

                    a{
                        color:$default-text-color;
                        font-weight: 400;

                        &:hover{
                            opacity: 1;
                        }

                    }

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }
    }


    &.widget_recent_entries {
	
	    .eltd-widget-title {
            margin: 40px 0 23px;
        }

        ul {

            li {
                margin: 0 0 12px;
                font-size: 15px;
                    a{
                        color:$default-text-color;
                    }

                > span {
                    display: block;
	                color:$default-info-color;
                    text-transform: uppercase;
                    font-size: 11px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .eltd-widget-title {
            margin: 0 0 18px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            color: $default-info-color;
            font-family: inherit;
            font-weight: 400;
            font-size: 11px !important;
			text-transform: uppercase;
            background-color: transparent;
            letter-spacing: 0.1em;

            &:after {
                content: ',';
                color: $default-text-color;
                margin: 0;
            }

            &:last-child {

                &:after {
                    display: none;
                }
            }

            &:hover {
                opacity:0.85;
            }
        }
    }

    &.eltd-blog-list-widget {

        .eltd-widget-title {
            margin-bottom: 28px;
        }
    }

}

.vc_wp_search {

    .widget {

        &.widget_search {


            form {

                > div {
                    background-color: #fff;
                    border: none;

                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;



            }


        }
    }
}

.eltd-side-menu{

    .widget {
        color: $default-info-color;

        &:last-child {
            margin: 0 !important;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
            color: $default-heading-color;
        }


        .eltd-widget-title {
            color: $default-info-color;
            margin: 0 0 27px;
        }

        a {
            color: #b1b2b2;

            &:hover {
                color: $first-main-color;
            }
        }

        table thead tr,
        table tbody tr {
            border-color: #b1b2b2;
        }

        table tbody th {
            border-right-color: #b1b2b2;
        }

        /*
        *   Specific styles
        */
        #wp-calendar {

            caption {
                color: #b1b2b2;
            }
        }

        &.widget_rss {

            .eltd-footer-widget-title {

                .rsswidget {
                    color: #fff;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-color: $default-border-color;
                }
            }

            input {
                color: #b1b2b2;

                @include eltdPlaceholder {
                    color: inherit;
                }

                &[type='submit'] {
                    color: #b1b2b2;
                }
            }

        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #b1b2b2;
                border-color: #b1b2b2;

                option {
                    background-color: #b1b2b2;
                }
            }

        }

        .widget_text {

        }

        &.widget_tag_cloud {

            a {
                color: #b1b2b2;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }

}
.eltd-page-footer {

    .widget {
	    color: #b1b2b2;
        margin-bottom: 28px;

        &.widget_nav_menu {


            ul {

                li{
                    a{  
                        position:relative;
                        color:#fff !important;
                        font-weight: 400;
                        letter-spacing: 2px;

                        &:after {
                            content: "";
                            position: absolute;
                            bottom: -3px;
                            left: 0;
                            width: 100%;
                            height: 1px;
                            background: #fff;
                            @include eltdTransition(all 0.2s cubic-bezier(0.32, 0.93, 0.49, 0.77) 0.05s);
                            @include eltdTransform(scale(0,1));
                        }

                        &:hover {

                            &:after { 
                                @include eltdTransform(scale(1,1));
                            }
                        }
                    }
                }
            }
        }
	
	    &:last-child {
		    margin-bottom: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
            margin: 0;
	        color: #fff;
        }

	
	    .eltd-widget-title {
		    color: #fff;
		    margin: 0 0 27px;
	    }
	
	    a {
		    color: #b1b2b2;
		
		    &:hover {
			    color: $first-main-color;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: #b1b2b2;
        }

        table tbody th {
            border-right-color: #b1b2b2;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #b1b2b2;
		    }
	    }
	
	    &.widget_rss {
		
		    .eltd-footer-widget-title {
			
			    .rsswidget {
				    color: #fff;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

            form {

                > div {
                    border-color: $default-border-color;
                }
            }

            input {
                color: #b1b2b2;
	            
	            @include eltdPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: #b1b2b2;
                }
            }

            button {

                &:hover {
                    opacity:0.85;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #b1b2b2;


                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_comments,
        &.widget_nav_menu {
            ul li {
                color: #b1b2b2 !important; /* overwrite color issue */
                a {
                    color: #b1b2b2;
                }
            }
        }

        &.widget_tag_cloud {


            a {
                color: #b1b2b2;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }

}

.eltd-top-bar {

	a {
		color: #969696;

		&:hover {
			color: $first-main-color;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;


        &.widget_text {
            margin: 0 8px 0 0;
            

        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }


    }


}




/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */