@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltd-comment-holder {
    @include eltdRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltd-comment-holder-inner {
        padding: 12px 0 20px;
    }

    .eltd-comment {

        border-bottom: 1px solid $default-border-color;
        padding-bottom: 25px;

        &.eltd-pingback-comment {

            .eltd-comment-text {
                padding: 0;
            }
        }
    }

    .eltd-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdRelativeHolderLayout();
            padding: 51px 0 0;
        }



        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children .eltd-comment {
            padding: 0 0 27px 99px;
        }
    }

    .eltd-comments-title {
        margin-bottom: 51px;

        > * {
            margin: 0;
        }
    }

    .eltd-comment-image {
        position: relative;
        display: block;
        top: -10px;
        left: 0;
        width: 95px;
        height: 95px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltd-comment-text {
        padding: 0 0 0 115px;
        min-height: 82px;

        .eltd-comment-info {
            @include eltdRelativeHolderLayout();
            margin: 6px 0 11px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 11px;
            font-weight: 500;
            text-transform: uppercase;

            &:hover {
                color: $default-heading-color;
            }
        }

        .eltd-comment-name {
            float: left;
            margin: 0;
        }

        .eltd-comment-date {
            float: left;
            margin: 0;
            font-size: 11px;
            text-transform: uppercase;
            font-weight: 400;
            letter-spacing: 1px;
        }

        .eltd-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltd-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .eltd-comment-form-inner {
        padding: 45px 0 78px;
    }

    .comment-reply-title {
        margin: 0 0 37px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltd-comment-holder {

        .eltd-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .eltd-comment-holder {

        .eltd-comment-list {

            .children {
                padding: 0;
            }
        }

        .eltd-comment-text {

            .eltd-comment-date,
            .eltd-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .eltd-comment-holder {

        .eltd-comment-image {
            float: none;
        }

        .eltd-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltd-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include eltdButtonDefaultStyle();
    @include eltdButtonMediumParams();
    @include eltdButtonSolidColor();
    -webkit-appearance: none !important;
    -webkit-border-radius: 0 !important;

    &:hover {
        color:#212121;
        border:1px solid #212121;
        background-color:transparent;
    }
}

.wpcf7-submit {
    -webkit-appearance: none !important; /* Keeps buttons sharp on Ios devices */
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.eltd-style-form textarea,
.wpcf7-form textarea {
    display: block;
    padding: 16px 18px;
}

div.wpcf7 .ajax-loader {
    position: absolute;
}

.eltd-two-columns-form-without-space {

    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltd-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.eltd-slider-is-moving {

        .owl-item {

            .eltd-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.eltd-slider-has-both-nav {

        .owl-nav {

            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdTransform(translateY($pos));

            }
        }
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            z-index: 3;
            opacity: 1 !important; /* overwrite default behaiver  */
            color: $default-heading-color;
            @include eltdTransform(translateY(-50%));
            @include eltdTransition(all .2s ease-out);

            &:hover {
                opacity: 0.8;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 20px;
                line-height: 1;
                font-family: 'Linearicons-Free';

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }

        .eltd-custom-nav & {
            background-color: #fff;
            display: inline-block;
            position: absolute;
            top: 50%;
            @include eltdTransform(translateY(-50%));
            left: 0;
            z-index: 1;
            height: 53px;
            line-height: 53px;
            padding: 0 23px;

            .eltd-custom-prev-icon,
            .eltd-custom-next-icon {
                -webkit-backface-visibility: hidden;
            }

            .owl-prev,
            .owl-next {
                position: relative;
                top: auto;
                left: auto;
                right: auto;
                @include eltdTransform(initial);
                float: left;

                .custom-line-top,
                .custom-line-bottom {
                    position: relative;
                    display: block;
                    height: 10px;
                    width: 1px;
                    background-color: $default-heading-color;
                    @include eltdTransition(background-color 0.15s cubic-bezier(0.09, 0.13, 0.13, 0.98), transform 0.15s cubic-bezier(0.09, 0.13, 0.13, 0.98));
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                }

                .eltd-number-custom-icon {
                    color: $default-text-color;
                    font-size: 14px;
                    font-weight: 400;
                    padding:0 8px;
                    position: relative;
                    top: -1px;
                    font-family: "Open Sans" !important; /* Font family for single room nav*/
                }

                .custom-line-top {
                    bottom: -2px;
                }

                .custom-line-bottom {
                    top: -2px;
                }
            }

            .owl-prev:hover {
                .custom-line-top {
                    @include eltdTransform(rotate(45deg));
                }
                .custom-line-bottom {
                    @include eltdTransform(rotate(-45deg));
                }

            }

            .owl-next:hover {
                .custom-line-top {
                    @include eltdTransform(rotate(-45deg));
                }
                .custom-line-bottom {
                    @include eltdTransform(rotate(45deg));
                }
            }
        }

        .eltd-custom-nav.eltd-custom-nav-right & {
            left: auto;
            right: 0;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 6px;
                height: 6px;
                margin: 0 6px;
                border-radius: 3em;
                background-color: $default-heading-color;
                border: 2px solid $default-heading-color;
                opacity: 0.5;
                box-sizing: border-box;
                @include eltdTransition(all .25s cubic-bezier(0.09, 0.13, 0.13, 0.98));
            }

            &:hover,
            &.active {

                span {
                    opacity: 1;
                    transform: scale(1.4);
				}
			}
		}

        .eltd-hide-pagination & {
            display: none !important;

        }
	}

    .eltd-testimonials-author-job-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .eltd-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -5px 0;

        > .eltd-slider-thumbnail-item {
            float: left;
            padding: 0 5px;
            box-sizing: border-box;
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltd-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltd-slider-thumbnail-item {
                    width: 100% / ($i+2);
                }
            }
        }

    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.eltd-404-page {

    .eltd-page-header,
    .eltd-mobile-header {
        display: none;
    }

    &.eltd-wrapper {
        padding: 0 !important;
    }

    .eltd-content {
        height: 100vh;
        margin-top: 0 !important;
        background-color: #282828;

        .admin-bar & {
            height: calc(100vh - 32px);
        }
    }

    .eltd-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .eltd-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 20%;
        text-align: center;

        .star-404:before {
            content: '\e033';
            font-family: ElegantIcons;
            color: #ffffff;
            font-size: 16px;
            padding: 0px 2px;
        }

        .eltd-404-subtitle {
            color: #ffffff;
            margin: 13px 0px 0px;
        }

        .eltd-404-subtitle-bold {
            font-weight: 700;
        }

        .eltd-404-text {
            font-size: 15px;
            color: #ffffff;
            max-width: 450px;
            position: relative;
            margin: 16px auto 0px;
        }

        .eltd-btn {
            margin: 30px 0 0;

            &.eltd-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-portrait {

        .eltd-page-not-found {
            padding: 40px 30px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltd-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: #fff;
        overflow: hidden;

        i {
            color: #fff;
            z-index: 100;
            position: relative;
        }

        &:after {
            content: '';
            position: absolute;
            z-index: 1;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: $default-heading-color;
            transform-origin: bottom center;
            @include eltdTransform(scale(1, 1));
            -webkit-transition: -webkit-transform 0.2s ease-out;
            transition: transform 0.2s ease-out;
        }

        &:hover {

            i {
                color: $default-heading-color;
            }

            &:after {
                height: 12px;
                opacity: 1;
                transform-origin: top center;
                @include eltdTransform(scale(1, 0));
            }

            span {
                &.eltd-icon-linear-icons {
                    &:before,
                    &:after {
                        top: calc(50% + 5px);
                        background-color:$default-heading-color;
                    }

                    &:before {
                        @include eltdTransform(rotate(135deg) scale(1,1) translate3d(-50%,-50%,0));
                    }

                    &:after {
                        @include eltdTransform(rotate(45deg) scale(1,1) translate3d(-50%,-50%,0));
                    }
                }
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;

        &.eltd-icon-linear-icons {
            &:before,
            &:after {
                content: "";
                position: absolute;
                font-size: 20px;
                z-index: 500;
                width: 14px;
                height: 2px;
                background-color: #fff;
                top: 50%;
                transform-origin: top center;
                @include eltdTransition(all .2s ease-out);
            }

            &:before {
                left: calc(50% - 17px);
                @include eltdTransform(rotate(180deg) scale(0.69,1) translate3d(-50%,-50%,0));
            }

            &:after {
                left: calc(50% + 2px);
                @include eltdTransform(rotate(0deg) scale(0.69,1) translate3d(-50%,-50%,0));
            }
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltd-self-hosted-video-holder {

    .eltd-video-wrap {

        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/

            .mejs-overlay-button {
                height: 40px;
                width: 40px;
                margin: -20px 0 0 -20px;
                background-size: cover;

            }
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Newsletter styles  - start
   ========================================================================== */

.eltd-contact-form-7-widget{

    .eltd-newsletter {
        position: relative;

        .eltd-newsletter-input {
            width: 100%;
            float: left;
            box-sizing: border-box;

            .wpcf7-form-control-wrap {
                input {
                    color: $default-info-color;
                    border: 1px solid $default-border-color;
                    font-size: 15px;

                    footer & {
                        border-color: #464646;
                        font-weight: 400;
                    }
                }


            }
        }

        .eltd-newsletter-submit {
            position: absolute;
            right: 1px;
            top: 1px;
            width: 55px;
            float: left;
            box-sizing: border-box;
            height: 48px;
            border-left: 1px solid $default-border-color;
            footer & {
                border-left-color: #464646;
            }

            &:before {
                display: block;
                font-family: "linea-arrows-10";
                content: "\e04b";
                font-size: 20px;
                position: absolute;
                left: calc(50% - 10px);
                top: calc(50%);
                @include eltdTransform(translate3d(0, -50%, 0));
                @include eltdTransition(all 0.2s cubic-bezier(0.32, 0.93, 0.49, 0.77));
            }
            input {
                padding: 0;
                width: 100%;
                height: 100%;
                opacity: 0;

            }

            &:hover {
                &:before {
                    @include eltdTransform(translate3d(3px, -50%, 0));
                }
            }

        }

    }

}



/* ==========================================================================
   #Newsletter styles  - end
   ========================================================================== */

.eltd-city {
    position: relative;
    max-width: 535px;

    .eltd-city-input {
        width: 100%;
        float: left;
        box-sizing: border-box;

        .wpcf7-form-control {
            border: 1px solid $default-info-color;
            height: 50px;
        }

    }

    .eltd-city-submit {
        position: absolute;
        right: 0;
        top: 0;
        width: 50px;
        float: left;
        box-sizing: border-box;
        height: 100%;
        border: none;


        &:before {
            display: block;
            font-family: "linea-basic-10";
            content: "&";
            font-size: 20px;
            position: absolute;
            right: 19px;
            top: 50%;
            @include eltdTransform(translateY(-50%));
            @include eltdTransition(color 0.16s cubic-bezier(0.09, 0.13, 0.13, 0.98));
        }
        input {
            padding: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
        }

    }

}

/* ==========================================================================
   #Datepicker styles  - end
   ========================================================================== */

#ui-datepicker-div.ui-widget-content {
    z-index: 1000 !important;
    background: $default-light-color;

    .ui-datepicker-calendar{
        background-color: transparent!important;
        border-collapse: separate;
        border-spacing: 3px 3px;
        border:1px solid $default-border-color;

        tr {
            border:1px solid transparent;


            td{
                position:relative;
                padding: 11px 16px;

                span{
                    font-weight: 500;
                    color:#bdbdbd;
                }

                a{
                    color:$default-heading-color;
                    font-weight: 500;
                    z-index: 10;

                    &:after {
                        content:'';
                        position:absolute;
                        top:0;
                        left:0;
                        right:0;
                        bottom:0;

                    }

                    &:hover:after {
                        background-color:rgba(235, 235, 235, 0.5);
                        z-index: 0  ;
                    }
                }

            }



        }
    }
}

#ui-datepicker-div {
    .ui-widget-header{
        background-color: $default-info-color!important;
        border-color:$default-info-color !important;
        padding: 10px 15px;

        > * {
            color:$default-light-color;
        }

        .ui-datepicker-prev  {
            float:left;
            width: 33%;
            cursor: pointer;

            &:before {
                content: "\e03f";
                font-family: "linea-arrows-10";
                font-size: 24px;
            }
        }
        span.ui-icon.ui-icon-circle-triangle-w {
            display:none;
        }

        .ui-datepicker-next{
            float:right;
            width:33%;
            text-align:right;
            cursor: pointer;

            &:before{
                content: "\e04b";
                font-family: "linea-arrows-10";
                font-size:24px;
            }

        }

        span.ui-icon.ui-icon-circle-triangle-e {
            display:none;
        }

        .ui-datepicker-title{
            display:inline-block;
            width:33%;
            font-size: 20px;
            text-align: center  ;
        }

    }

    .ui-widget-header {
        font-family:$heading-text-font !important;
    }

    .ui-datepicker-calendar thead{
        font-family:$heading-text-font !important;
    }
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    font-size: 14px;
}




