
// widgets spacing
$widget-bottom-margin: 45px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;

// widget color
$widget-background-color: transparent;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: inherit;
$widget-heading-color: inherit;

// links
$widget-link-color: $first-main-color;
$widget-link-hover-color: darken($default-text-color, 35%);

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

aside.eltd-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .eltd-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.eltd-sidebar,
.wpb_widgetised_column {
    @import 'widgets/custom-font-widget';
    @import 'widgets/default-wordpress-widgets';
    @import 'widgets/icon-widget';
    @import 'widgets/instagram-widget';
    @import 'widgets/raw-html-widget';
    @import 'widgets/separator-widget';
    @import 'widgets/social-icon-widget';
    @import 'widgets/sticky-sidebar-widget';
    @import 'widgets/twitter-widget';

}