/* ==========================================================================
   Instagram widget style - begin
   ========================================================================== */

aside.eltd-sidebar,
.wpb_widgetised_column {
    
    .widget {
        
        &.widget_eltd_instagram_widget {
            
            .eltd-widget-title {
                margin: 35px 0 25px;
            }
        }
    }
}

.eltd-instagram-feed {
    list-style: none;
    padding: 0;
    margin: 0;
    
    li {
        float: left;
        box-sizing: border-box;
        border: none !important;
        overflow: hidden;
        
        a {
            position: relative;
            display: block;
            overflow: hidden;
            @include eltdImageOverlayHoverStyle();

            .eltd-social-icon-widget {
                position: absolute;
                top: 50%;
                left: 50%;
                opacity: 1;
                z-index: 50;
                font-size:25px;
                color: #fff;
                opacity:0;
                @include eltdTransition(all .35s cubic-bezier(0.32, 0.93, 0.49, 0.77) 0.05s);
                @include eltdTransform(translate3d(-50%,-50%,0));
            }

            img {
                width: 100%;
                display: block;
                @include eltdTransform(scale(1.005) translateZ(0));
                @include eltdTransition(all .35s cubic-bezier(0.32, 0.93, 0.49, 0.77) 0.05s);
            }

            &:hover {
                .eltd-social-icon-widget { 
                    opacity:1;
                }

                img { 
                    @include eltdTransform(scale(1.05) translateZ(0));
                }
            }
        }
    
    }
    
    &.eltd-instagram-gallery {
        
        $gallery_space_label: ('no', 'tiny', 'small', 'normal');
        $gallery_space_width: (0, 5, 10, 15);
        
        @for $i from 0 to length($gallery_space_label) {
            &.eltd-#{nth($gallery_space_label,$i+1)}-space {
                $column_width: nth($gallery_space_width, $i+1);
                $column_width_margin: $column_width * 2;
                
                @if ($column_width == 0) {
                    margin: 0;
                } @else {
                    margin: 0 -#{$column_width}px -#{$column_width_margin}px;
                }
                
                li {
                    padding: 0 #{$column_width}px;
                    margin: 0 0 #{$column_width_margin}px;
                }
            }
        }
        
        $gallery_columns: ('2', '3', '4', '6', '9');
        @for $i from 0 to length($gallery_columns) {
            &.eltd-col-#{nth($gallery_columns, $i+1)} {
                $column_width: 100%/($i+2);
                
                @if ($i == 3) {
                    $column_width: 100%/6;
                } @else if ($i == 4) {
                    $column_width: 100%/9;
                }
                
                li {
                    width: $column_width;
                }
            }
        }
    }
    
    &.eltd-instagram-carousel {
        
        li {
            position: relative;
            width: 100%;
            margin: 0;
            padding: 0;
            
            a {
                position: relative;
                display: block;
                height: 100%;
            }
        }
    }
}
/* ==========================================================================
   Instagram widget style - end
   ========================================================================== */